/*------------------------------------------------------*/
.topIn-enter {
  -webkit-transform: translateY(-60%);
      -ms-transform: translateY(-60%);
          transform: translateY(-60%);
  opacity: .01;
}
.topIn-enter.topIn-enter-active {
  -webkit-transform: translateY(0%);
      -ms-transform: translateY(0%);
          transform: translateY(0%);
  opacity: 1;
  -webkit-transition: opacity 0.3s ease, -webkit-transform 0.3s ease-out;
  transition: opacity 0.3s ease, -webkit-transform 0.3s ease-out;
  -o-transition: opacity 0.3s ease, transform 0.3s ease-out;
  transition: opacity 0.3s ease, transform 0.3s ease-out;
  transition: opacity 0.3s ease, transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}
.topIn-exit {
  opacity: 1;
  -webkit-transform: translateY(0%);
      -ms-transform: translateY(0%);
          transform: translateY(0%);
}
.topIn-exit.topIn-exit-active {
  opacity: .01;
  -webkit-transform: translateY(-60%);
      -ms-transform: translateY(-60%);
          transform: translateY(-60%);
  -webkit-transition: opacity 0.2s ease, -webkit-transform 0.3s ease-out;
  transition: opacity 0.2s ease, -webkit-transform 0.3s ease-out;
  -o-transition: opacity 0.2s ease, transform 0.3s ease-out;
  transition: opacity 0.2s ease, transform 0.3s ease-out;
  transition: opacity 0.2s ease, transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}
